.invitation-text {
    text-align: center;
    padding: 20px;
    font-family: 'SUIT-Regular';
    font-size: 13px;
    letter-spacing: 1.4px;
    line-height: 2;
  }
  
  .invitation-text h2 {
    font-size: 24px;
    margin-bottom: 20px;
  }
  
  .invitation-text p {
    margin-bottom: 0px;
  }
  
  .extra-space {
    margin-top: 60px; /* 위쪽에 20px의 간격을 추가 */
  }

  .div-margin {
    margin-bottom: 20px; /* 위쪽에 20px의 간격을 추가 */
    padding: 30px;
  }

  .background-color{
    background-color: #f9f8f3;
    box-shadow:'rgba(0, 0, 0, 0.1) 0px 0px 8px';
  }