/* Gallery container */
.gallery-container {
    width: 100%;
    max-width: 480px; /* Max width of gallery */
    margin: auto;
    position: relative;
}

/* Image grid */
.gallery-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr); /* Set to 2 columns */
    gap: 10px; /* Space between images */
    padding: 10px; /* Internal padding */
}

/* Images inside the grid */
.gallery-grid img {
    width: 100%; /* Adjust image size to grid cell */
    height: auto; /* Maintain aspect ratio */
    border: 2px solid #fff; /* White border */
    box-sizing: border-box; /* Include padding and border in element's total width and height */
    border-radius: 10px; /* Rounded corners */
    transition: transform 0.3s ease; /* Add scaling animation */
    
}

.gallery-grid img:hover {
    transform: scale(1.05); /* Scale up on hover */
}


/* Modal overlay */
.modal-overlay {
    position: fixed; /* Fixed position */
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.8); /* Semi-transparent background */
    display: flex;
    justify-content: center; /* Center content */
    align-items: center; /* Center vertically */
    z-index: 1000; /* Above other elements */
}

/* Modal content */
.modal-content {
    position: relative;
    max-width: 100%; /* Max width */
    max-height: 100%; /* Max height */
    overflow: hidden; /* Hide overflow */
}

/* Modal image */
.modal-image {
    width: auto; /* Auto width */
    height: auto; /* Auto height */
    max-width: 100%; /* Max width */
    max-height: 100vh; /* Max height based on viewport height */
    border-radius: 10px; /* Rounded corners */
}

/* Close button */
.close-button {
    position: absolute; /* Relative to modal */
    top: 10px;
    right: 10px;
    background-color: rgba(255, 255, 255, 0.8);
    border: none;
    padding: 5px 10px;
    cursor: pointer; /* Hand pointer */
    border-radius: 5px; /* Rounded corners */
}



/* Prev and Next buttons */
.prev-button, .next-button {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    border: none;
    padding: 10px;
    cursor: pointer;
    border-radius: 5px;
    z-index: 10002; /* Above modal content */
    background: none;  /* 배경 제거 */
    border: none;      /* 테두리 제거 */
    color: #fff;    /* 텍스트 색상 상속 */
    font-size: 24px;   /* 원하는 폰트 크기로 설정 */
    cursor: pointer;    /* 마우스 포인터를 손 모양으로 변경 */
    padding: 0;        /* 패딩 제거 */
}

.prev-button {
    left: 30px; /* Position to the left */
}

.next-button {
    right: 30px; /* Position to the right */
}


.selected-image {
    /*transition: transform 0.2s ease; /* 부드러운 전환 효과 */
}

.selected-image-container{
    height: 100vh; /* 화면 높이에 맞춤 */
    width: 100vw;  /* 화면 너비에 맞춤 */
    position: fixed; /* 스크롤에 영향을 받지 않도록 고정 */
    top: 0;         /* 상단에 맞춤 */
    left: 0;        /* 좌측에 맞춤 */
    background-color: rgba(0, 0, 0, 0.8); /* 배경을 어둡게 하여 이미지가 두드러지도록 */
    display: flex;  /* flexbox로 중앙 정렬 */
    justify-content: center; /* 수평 중앙 정렬 */
    align-items: center; /* 수직 중앙 정렬 */
    z-index: 1000; /* 다른 요소들 위에 표시되도록 */
    cursor: grab;
}

.selected-image-container img{
    object-fit: contain;
    width: 100%;
    height: 100%;
}

/* PC 모드에서만 max-width를 400px로 제한 */
@media (max-width: 1023px) {
    .selected-image-container img {
        padding-bottom: 16px;
    }
}
